@font-face {
  font-family: Avenir-Book;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

html {
  --font-family: Avenir-Book, sans-serif;
  --text-shadow: 1px 4px 4px #00000040;
  --box-shadow: 0 1px 8px 0 #3d4e611a;
  --box-shadow-for-big: 0 4px 4px 0 #00000040;
  --box-shadow-hover: 0 3px 8px 0 #3d4e6133;
}

.form {
  margin-top: 20px;
}

.form__input {
  box-sizing: border-box;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  display: block;
}

.form__input--big {
  box-shadow: var(--box-shadow);
  font-family: var(--font-family);
  background: url("Search.ccd3c07b.svg") 25px no-repeat;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 300;
}

.form__input:hover {
  box-shadow: var(--box-shadow-hover);
}

.form:hover:first-child, .form:focus:first-child {
  box-shadow: var(--box-shadow-for-big);
}

.form__input:focus {
  text-shadow: var(--text-shadow);
  box-shadow: var(--box-shadow-hover);
  background: url("Search.ccd3c07b.svg") 25px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-family: Avenir-Heavy, sans-serif;
}

.form__input--small {
  box-shadow: var(--box-shadow);
  text-shadow: var(--text-shadow);
  font-family: var(--font-family);
  background: url("Icon11x11.5656828a.svg") 13px no-repeat;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  font-weight: 400;
}

.form__input--small:focus {
  box-shadow: none;
  text-shadow: none;
  background: url("Icon11x11.5656828a.svg") 13px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
}
